/* 
  上面回傳為json server的mock data
  如果之後使用server side的話, 外面還需要再包一層
  { 
    apiVersion: "0.1",
    // 資料  
    data: {
      pageIndex: 1,
      totalPages: 122,
      items: [{...}, {...} ...]
    },
    // 錯誤資訊 如果有錯誤才會顯示
    error: {
      code: 404,
      message: '...'
    }
  }
*/

function isJson(str) {
  try {
      JSON.parse(str);
  } catch (e) {
      return false;
  }
  return true;
}

export const fetchAPI = (url, options = {}, oriData = []) => {
  
  let hasCalled = false, currentData = {};

  // 傳入redux state，並看是否已經call過
  oriData.forEach((data, i) => { 
    if (data.requestURL === url) {
      hasCalled = true;
      currentData = data.items;
    }
  });
  // 
  if (hasCalled) {
    return new Promise((resolve, reject) => { 
      console.log('hasCalled', url);
      resolve({ hasCalled: hasCalled, items: currentData });
    });
  }
  // fillInHeader
  // const access_token = isJson(localStorage.user) ? JSON.parse(localStorage.user).access_token : '';
  // const authHeaderOptions = (url.toLowerCase()).indexOf('login') === -1 ?
  //   { ...options, headers: { ...options.headers, Authorization: access_token } } : options;

  return fetch(url, options)
    .then((response) => {
      const resStatus = response.status;
      return response
        .json()
        .then(res => {
          let formatReturnObj = {};
          if (response.status >= 400 && response.status < 600) {
            // in case of 401 Unauthorized Error and not in login page.
            if (response.status === 401 && (url.toLowerCase()).indexOf('login') === -1) {
              // localStorage.setItem('user', '');
              document.cookie = "user=; expires=" + new Date().toUTCString();
              window.location.reload();
            }
            // error
            formatReturnObj = {
              requestURL: url,
              error: true,
              message: res.message
            }
          } else {
            formatReturnObj = {
              requestURL: url,
              status: resStatus,
              items: res
            }
          }
          return formatReturnObj;
        });
    })
    .catch((error) => {
      return {
        requestURL: url,
        error: true,
        message: 'Bad Request'
      }
      // console.log(error);
    });
}

export const fetchRequestHeader = {
  'Accept': 'application/json',
  'Content-Type': 'application/json; charset=utf-8',
  // 'Access-Control-Allow-Origin' : '*',
  'Access-Control-Allow-Methods': 'POST, GET, PATCH, DELETE, PUT',
  // 'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept, Authorization',
  // 'Authorization' : ''
}
export const apiURL = (
  typeof process !== "undefined" &&
  typeof process.env.NODE_ENV !== "undefined" &&
  process.env.NODE_ENV.trim() === 'development') ? 'http://localhost:4000' : '/api';



