import { fetchAPI, apiURL } from './core';
export * from './login';

export const fetchAboutData = () => {
  return fetchAPI(`${apiURL}/about`, {
    method: 'GET'
  })
}

export const fetchCompanyData = (oriData = [], page = 1, pageSize = 10, keyword = '') => {
  let requestURL =  apiURL + "/company?";
      requestURL += `_page=${page + 1}&_limit=${pageSize}&name_like=${keyword}`;
      
  return fetchAPI(requestURL, {
    method: 'GET'
  }, oriData);
}

export const fetch401 = () => {
  let requestURL = apiURL + "/s401";
      
  return fetchAPI(requestURL, {
    method: 'GET'
  });
}



